.container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
}

.name {
  font-size: 18px;
  font-weight: bold;
}
.number {
  font-size: 14px;
}

.btn {
  margin-left: 20px;
  padding: 8px 16px;
  align-items: center;
  justify-content: center;
  font-size: 16px;
  font-weight: bold;
  border: 1px solid #e9e9e9;
  border-radius: 5px;
}

.btn:hover {
  color: white;
  background-color: red;
  border: 1px solid red;
}
